// Import utilities
const {
	isDefined,
	getUniqueID
} = _uo.utility;

import {
	parseDynamicSentence
} from '@builder/utilities';

import {
	getConditionPostData
} from '@builder/item/conditions/utilities';

import {
	getIntegrationByCode
} from '@builder/integrations/utilities';

/**
 * Creates a condition
 */
export default class Condition {
	// Properties
	integrationCode = '';

	conditionCode = '';

	/**
	 * The value of the fields
	 *
	 * @type {Object}
	 */
	fields = {};

	/**
	 * This is ONLY used if the condition is missing
	 *
	 * @type {Object}
	 */
	backup = {};

	/**
	 * The ConditionsGroup of this condition
	 *
	 * @type {ConditionGroup}
	 */
	conditionsGroup;

	/**
	 * Creates an unique ID
	 *
	 * @return {undefined}
	 */
	constructor() {
		// Create unique ID
		this.id = getUniqueID();
	}

	/**
	 * Checks whether a condition is missing
	 *
	 * @return {Boolean} True if it's missing
	 */
	get isMissing() {
		// Check if the condition is defined in the PHP
		return ! isDefined( this.conditionPostData );
	}

	/**
	 * Returns an object with sanitazed data for the backend
	 *
	 * @return {Object} Sanitazed data
	 */
	get formattedForMeta() {
		// Default response
		let conditionForMeta = {};

		/**
		 * The backend expects
		 *
		 * {
		 * 	 integration: 'WP', // String with the integration code
		 * 	 condition: 'TOKEN_MEETS_CONDITION', // String with the condition code
		 * 	 fields: { // Object with the field values
		 * 	   FIELD_ID: 'Field value',
		 * 	   FIELD_ID_2: 'Field value'
		 * 	 },
		 * 	 backup: { // Object with data used when the condition is missing
		 * 	   nameDynamic: '', // The name of the sentence
		 * 	   integrationName: '', // The name of the integration
		 * 	 }
		 * }
		 */
		conditionForMeta = {
			integration: this.integrationCode,
			condition: this.conditionCode,
			fields: this.fields,

			// We will save this just in case the condition gets removed in the future
			// If the condition is missing, DON'T UPDATE the value, otherwise it will get removed
			backup: this.isMissing ? this.backup : {
				// Get the dynamic name of the condition
				nameDynamic: this.conditionPostData.dynamic_name,

				// Integration name
				integrationName: this.integration.name,
			}
		};

		return conditionForMeta;
	}

	/**
	 * Returns the dynamic sentence of the condition with the current values of the fields
	 *
	 * @return {Node} A node with the sentence
	 */
	get $dynamicSentence() {
		// Create node
		const $sentence = parseDynamicSentence( {
			dynamicSentence: ! this.isMissing ? this.conditionPostData.dynamic_name : this.backup.nameDynamic,
			fields: this.fields,
		} );

		return $sentence;
	}

	/**
	 * Returns the condition base data
	 *
	 * @return {Object} The condition base data
	 */
	get conditionPostData() {
		return getConditionPostData( {
			integrationCode: this.integrationCode,
			conditionCode: this.conditionCode
		} );
	}

	/**
	 * Returns the integration of the condition
	 *
	 * @return {Integration} The integration
	 */
	get integration() {
		return getIntegrationByCode( {
			integrationCode: this.integrationCode,
		} );
	}
}
